import { Component, ChangeDetectorRef, OnInit, HostListener, ViewChild, EventEmitter, ElementRef, Input, Output } from '@angular/core';

@Component({
  selector: 'double-slider',
  templateUrl: './double-slider.component.html',
  styleUrls: ['./double-slider.component.css']
})
export class DoubleSliderComponent implements OnInit {
  @Input("tooltip") tooltip = false;
  @Input("tooltipSymbol") tooltipSymbol = "";
  @Input("min") minValue: number;
  @Input("max") maxValue: number;
  @Input("increment") increment: number = 0;
  @Input("disable") disable: boolean = false;
  @Output("minChange") minChange = new EventEmitter();
  @Output("maxChange") maxChange = new EventEmitter();
  @ViewChild("dbSlider1") dbSlider: ElementRef;
  @ViewChild("slideMin") slideMin;
  @ViewChild("slideMax") slideMax;
  @ViewChild("sliderProgress") progress;
  @Input("sliderMiddle") sliderMiddle=true; 
  @Input("sliderMinIcon") sliderMinIcon="";
  private element;
  private lastWidth;
  private mouseDownHandle: boolean = false;
  private width: number;
  private total: number = 0;
  public min;
  public max;
  private getSlider;
  private slideMinPos;
  private slideMaxPos;
  private sliderWidth;
  private halfSliderWidth;
  private minPos;
  private maxPos;
  private last; 
  public test = 0;
  private initalPosition;
  private minTooltip;
  private maxTooltip;
  constructor(private cdr: ChangeDetectorRef) { }
  ngOnInit() {
  }

  private resetPosition() {
    let pos = -(this.halfSliderWidth);
    this.sliderChange("min", pos, this.minValue);
    pos = (this.halfSliderWidth);
    this.sliderChange("max", pos, this.maxValue);
    this.minPos = 0;
    this.maxPos = this.width;
    this.last = 0;
    this.progressFn(this.progress.nativeElement, this.minPos);
  }
   onmousedown(e) {
    if (!this.disable) {
      e.preventDefault();
      this.element = e.target;
      this.mouseDownHandle = true;
      e.stopPropagation();
    }

  }
  onResize(e) {
    this.width = this.dbSlider.nativeElement.clientWidth;
    this.maxPos = +(((this.maxPos) / this.lastWidth) * this.width);
    this.minPos = +((this.minPos / this.lastWidth) * this.width);
    this.lastWidth = this.width;
    this.parentHandler(this.minPos, "min");
    this.parentHandler(this.maxPos, "max")
    this.last = this.minPos;
    if (this.checkGreater()) {
      this.progressFn(this.progress.nativeElement, this.minPos);
    }
  }
  private sliderChange(ele, pos, value) {
    if (ele === "min") {
      this.slideMin.nativeElement.style.webkitTransform = `translate(${pos}px,0px)`;
      this.minChange.emit(value);
      this.minTooltip = value;
    } else {
      this.slideMax.nativeElement.style.webkitTransform = `translate(${pos}px,0px)`;
      this.maxChange.emit(value);
      this.maxTooltip = value;
    }
  }
  ngAfterViewInit() {
    setTimeout(() => {
      this.sliderWidth = this.slideMax.nativeElement.clientWidth;
      this.halfSliderWidth = this.sliderWidth / 2;
      this.width = this.dbSlider.nativeElement.clientWidth;
      this.lastWidth = this.width;
      this.initalPosition = this.dbSlider.nativeElement.offsetParent.offsetLeft + this.dbSlider.nativeElement.offsetLeft;
      this.minPos = 0;
      this.maxPos = this.width;
      this.total = this.maxValue - this.minValue;
      this.min = this.minValue | 0;
      this.max = this.total > 0 ? this.maxValue : this.width;
      this.minTooltip = this.min;
      this.maxTooltip = this.max;
      this.resetPosition();
      this.last = 0;
    });
  }
  private getPosition(e) {
    let touch = e.type.indexOf('touch') === 0;
    let clientX = touch ? e.touches[0].clientX : e.clientX;
    return clientX;
  }
  private parentHandler(e, checkS) {
    if (!this.disable) {
      let totalPos = 0;
      if (!checkS) {
        let pos = this.getPosition(e);
        totalPos = pos -  (this.dbSlider.nativeElement.offsetParent.offsetLeft + this.dbSlider.nativeElement.offsetLeft);
      } else {
        totalPos = Math.floor(e);
      }
      let value = this.evaluateValue(totalPos);
      if ((((totalPos <= this.width / 2) && this.max >= value) || value <= this.min) || checkS === "min") {
        this.minPos = totalPos;
        this.min = value;
        this.element = this.slideMin.nativeElement;
        let pos = totalPos - this.halfSliderWidth;
        this.sliderChange("min", pos, value);
      } else {
        this.maxPos = totalPos;
        this.max = value;
        this.element = this.slideMax.nativeElement;
        let pos = -(this.width - totalPos - this.halfSliderWidth);
        this.sliderChange("max", pos, value);
      }
      this.getSliderValue();
      if (this.checkGreater() && !checkS) {
        this.progressFn(this.progress.nativeElement, this.minPos);
      }
    }

  }
   parent(e) {
    e.preventDefault();
    this.parentHandler(e, false)
  }

  private onmouseup(e) {
    this.mouseDownHandle = false;
  }
  private checkGreater() {
    return this.max >= this.min;
  }

  @HostListener('document:mousemove', ['$event']) @HostListener('document:touchmove', ['$event']) onMove(e) {
    if (this.mouseDownHandle && !this.disable) {
      e.preventDefault();      
      let check = this.getPosition(e) - ( this.dbSlider.nativeElement.offsetParent.offsetLeft + this.dbSlider.nativeElement.offsetLeft);     
      if ((check <= this.width + 4) && (check - 2 >= 0)) {   
        this.getSliderValue();    
        let value = 0;
        if (this.getSlider === "min") {
          this.minPos = check;
          this.min = value = this.evaluateValue(check);
          if (this.checkGreater()) {
            let pos = this.minPos - this.halfSliderWidth;
            this.sliderChange("min", pos, value);
          }
        } else {
          this.maxPos = check;
          this.max = value = this.evaluateValue(this.maxPos);
          if (this.checkGreater()) {
            let pos = -(this.width - check - this.halfSliderWidth);
            this.sliderChange("max", pos, value);
          }
        }
        if (this.checkGreater()) {
          this.progressFn(this.progress.nativeElement, this.minPos);
        }
      }
    }

  }
  @HostListener('document:mouseup', ['$event']) @HostListener('document:touchend', ['$event']) mouseup(e) {
    // e.preventDefault();
    this.mouseDownHandle = false;
  }
  private progressFn(progress, pos) {
    // if (this.sliderMiddle) {
      let check = (pos !== false ? (this.maxPos - this.minPos) / this.width : 1);
      if (this.getSlider === "min") {
        this.last = pos;
        progress.style.webkitTransform = `translate(${pos}px,0) scale(${check},1)`;
      }
      else {
        progress.style.webkitTransform = `translate(${this.last}px,0) scale(${check},1)`;
      }
    // }
  }
  private evaluateValue(vals) {
    let increment = this.increment ? (this.total / this.increment) : 100;
    let val = Math.floor((vals / this.width) * increment);
    let value = (this.total / increment) * val;
    return value + this.minValue;
  }
  private decodeValue(element, val) {
    val = val - this.minValue;
    let val1 = this.width / (this.total / val);
    return val1;
  }
  private getSliderValue() {
    if (this.element.classList.contains("slider-min")) {
      this.getSlider = "min";
    }
    else if (this.element.classList.contains("slider-max")) {
      this.getSlider = "max";
    }
  }
  public reset() {
    this.resetPosition();
  }

  public setMax(val) {
    val = parseInt(val);
    if (val <= this.maxValue && val >= this.min && !this.disable) {
      this.getSlider = "max";
      let element = this.slideMax.nativeElement;
      let pos = -(this.width - this.decodeValue(element, val) - this.halfSliderWidth);
      this.sliderChange("max", pos, val);
      this.max = val;
      this.maxPos = this.width + pos;
      this.progressFn(this.progress.nativeElement, this.minPos);
    }
  }
  public setMin(val) {
    val = parseInt(val);
    if (val >= this.minValue && val <= this.max && !this.disable) {
      this.getSlider = "min";
      let element = this.slideMin.nativeElement;
      let pos = this.decodeValue(element, val) - this.halfSliderWidth;
      this.sliderChange("min", pos, val);
      this.minPos = pos;
      this.min = val;
      this.progressFn(this.progress.nativeElement, this.minPos);
    }
  }
  
}

